html {
  scroll-behavior: smooth;
}

body {
  font-family: Inter;
  font-size: 16px;
  line-height: 1.4;
  color: var(--Gray-700);
  background: var(--Gray-25);
}

.main-container {
  width: 100%;
  padding: 0 32px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 992px) {
  .main-container {
    padding: 0 16px;
  }
}
@media (min-width: 576px) {
  .main-container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .main-container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .main-container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .main-container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .main-container {
    max-width: 1320px;
  }
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

a {
  text-decoration: none;
  color: var(--Primary-600);
}
a:hover {
  color: var(--Primary-500);
}

.btn {
  padding: 8px 20px;
  outline: 0;
  text-decoration: none;
  font-size: 16px;
  line-height: 1;
  letter-spacing: 0.5px;
  transition: all 0.3s;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
}
.btn.primary {
  background-color: var(--Primary-50);
  border: 1px solid var(--Primary-50);
  color: var(--Primary-600);
  box-shadow: 0 3px 5px 0 rgba(47, 85, 212, 0.1);
}
.btn.primary:hover {
  background-color: var(--Primary-100);
  border: 1px solid var(--Primary-100);
}
.btn.round {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 8px;
  border-radius: 50%;
}
.btn.round svg path {
  fill: var(--Primary-600);
}
.btn.btn-link {
  padding: 0;
  border: none;
  font-weight: 500;
  color: var(--Primary-600);
}
.btn.btn-link:hover {
  color: var(--Primary-500);
}

.dropdown-wrap {
  position: relative;
}

.dropdown-elem {
  position: absolute;
  min-width: 180px;
  visibility: hidden;
  opacity: 0;
  top: 48px;
  right: 0;
  background-color: var(--Primary-50);
  border: 1px solid var(--Primary-50);
  box-shadow: 0 0 8px rgba(14, 22, 131, 0.2);
  color: var(--Primary-600);
  border-radius: 6px;
  padding: 8px;
  transition: all 0.3s;
}
.dropdown-elem.vision {
  visibility: visible;
  opacity: 1;
}

.page-main {
  margin-top: 100px;
}

.page-title {
  color: var(--Gray-800);
  margin-top: 0;
}

.text-center {
  text-align: center;
}

.header-pages {
  position: sticky;
  top: 0;
  background: var(--Gray-25);
}

.header-wrap-shadow {
  box-shadow: 0 0 10px rgba(14, 22, 131, 0.2);
}

.container-header {
  display: flex;
  align-items: center;
  padding-top: 16px;
  padding-bottom: 16px;
}

.site-brand {
  flex: none;
  color: var(--Primary-600);
}
.site-brand:hover {
  color: var(--Primary-500);
}

.site-brand-title {
  font-size: 22px;
  font-weight: 600;
  line-height: 24px;
  margin: 0;
}

.primary-nav {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-left: auto;
}

.header-nav {
  display: flex;
  align-items: center;
  gap: 20px;
  width: 100%;
}

.footer-pages {
  margin-top: auto;
  background: var(--Primary-100);
}

.container-footer {
  padding-top: 40px;
}

.footer-top {
  display: flex;
  gap: 48px;
  padding-bottom: 20px;
}

.footer-brand {
  flex: 1;
}
.footer-brand__site-title {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 24px;
}
.footer-brand__site-description {
  margin-bottom: 24px;
}
.footer-brand__social-media {
  display: flex;
  gap: 16px;
  align-items: center;
}
.footer-brand__social-media__item {
  display: flex;
}
.footer-brand__social-media__item svg {
  width: 32px;
  height: 32px;
}
.footer-brand__social-media__item img {
  width: 32px;
  height: 32px;
}
.footer-brand__social-media__item:hover {
  filter: grayscale(1);
}

.footer-nav {
  flex: 2;
  display: flex;
  gap: 24px;
}

.footer-nav-item {
  flex: 1;
}
.footer-nav-item__title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}
.footer-nav-item ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer-nav-item ul li a {
  color: var(--Gray-600);
}
.footer-nav-item ul li a:hover {
  color: var(--Gray-500);
}

.footer-copyright {
  font-size: 14px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 1px solid var(--Gray-300);
}